import Vue from 'vue'
import Router from 'vue-router'

// import index from 'components/home/index'
// import classify from 'components/classify/classify'
import shopcart from 'components/shopped/shopped'
// import subject_List from 'components/classify/subject_List'
// import goods_list from 'components/list/goods_list'
// import goods_type_list from 'components/list/goods_type_list'
// import details from 'components/Details/details'
// import user from 'components/user/user'
// import error from 'components/common/error'
import App from '@/App'
Vue.use(Router)

const scrollBehavior = (to, from, savedPosition) => {
    if(savedPosition){
        return savedPosition
    }else{
        return { x: 0, y: 0 }
    }
}
export default new Router({
  linkActiveClass: 'active',
  mode: 'history',
  scrollBehavior ,
  routes: [
    {
      path: '',
      name: 'App',
      redirect: "/index",
      component:r => require.ensure([], () => r(require('components/home/index')), 'index'),
      meta:{ keepAlive: true }
    },
    {
      path: '/index',
      name: 'index',
      component:  r => require.ensure([], () => r(require('components/home/index')), 'index'),
      meta:{ keepAlive: true }
    },
    {
      path: '/classify',
      name: 'classify',
      component: r => require.ensure([], () => r(require('components/classify/classify')), 'classify'),
      
    },
    {
      path: '/subject_List',
      name: 'subject_List',
      component: r => require.ensure([], () => r(require('components/classify/subject_List')), 'subject_List'),

    },
    {
      path: '/classify_goods_list',
      name: 'goods_list',
      component:  r => require.ensure([], () => r(require('components/list/goods_list')), 'goods_list'),

    },
    {
      path: '/classify_goods_type_list',
      name: 'goods_type_list',
      component: r => require.ensure([], () => r(require('components/list/goods_type_list')), 'goods_type_list'),

    },
    {
      path: '/details_GetGood_Desc',
      name: 'details',
      component: r => require.ensure([], () => r(require('components/Details/details')), 'details'),
    },
    {
      path: '/shopcart',
      name: 'shopcart',
      // component:  r => require.ensure([], () => r(require('components/shopped/shopped')), 'shopcart'),
      component:shopcart
    },
    {
      path: '/user',
      name: 'user',
      component:  r => require.ensure([], () => r(require('components/user/user')), 'user'),
    },
    {
      path: '/register',
      name: 'reg',
      component: r => require.ensure([], () => r(require('components/user/register')), 'reg')
    },
    {
      path: '/login',
      name: 'login',
      component: r => require.ensure([], () => r(require('components/user/login')), 'login')
    },
    {
      path: '/search',
      name: 'search',
      component: r => require.ensure([], () => r(require('components/classify/search')), 'search')
    },
    {
      path:'*',
      component: r => require.ensure([], () => r(require('components/common/error')), 'error'),
    }
  ],
})



